@import url(https://fonts.googleapis.com/css2?family=Pattaya&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#Header {
  /* display: inline-flexbox; */
  width: 100%;
  position: fixed;
  margin-bottom: auto;
  padding:5px;
  top: 0;
  background-color: white;
  z-index: 1;
}

#NavbarLogo{
  float:left;
  max-height: 50px;
  padding:none;
  margin:none;
}

#ExpandNavbar{
  font-size: 30px;
  margin-right: 30px;
  color: black;
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  float: right;

}

.NavbarList{
  list-style-type: none;
  font-weight: bold;
  height: 0;
  overflow: hidden;
  text-align: center;
  padding-top:10px;
}


.show-nav{
  height: 140px;
}

.NavbarListItem{
  padding-right: 20px;
  padding-top: 10px;
}

.InlineLink{
  color: black;
  text-decoration: none;

}
.InlineLink:hover{
  color: gray;
  text-decoration:none;
}

.Spacer{
  margin-bottom: 7vh;
}

@media screen and (min-width: 768px) {
  button {
    display: none;
  }

  .show-nav {
    height: auto;
    display: flex;
  }

  .NavbarList {
    height: auto;
    display: flex;
  }
  .NavbarListItem {
    padding: 0 15px;
  }

}

html, body {
