.container {
  position: relative;
  width: 100%;
  max-width: 400px;
}

.container img {
  width: 100%;
  height: auto;
  -webkit-filter: brightness(30%);
          filter: brightness(30%);
}

.container .btn {

  position: absolute;
  border: solid;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 10%);
  -ms-transform: translate(-50%, -50%);
  background-color: transparent;
  color: white;
  font-size: 2em;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
}

.container .link {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -110%);
  -ms-transform: translate(-50%, -50%);
  color: white;
  font-size: 2em;
  font-weight: bold;
  /* padding: 12px 24px; */
  border-radius: 5px;
  text-align: center;
}

.container .btn:hover {
  background-color: black;
}

